.component {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  list-style: none;
  background: #fff;
  margin: 0;
  padding: 0;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  width: 24rem;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.component > li {
  -ms-flex: none;
      flex: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: rgba(0, 0, 0, 0.38);
  font-weight: bold;
  font-size: 1rem;
  padding: 1rem;
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.component > li:hover {
  background: rgba(0, 0, 0, 0.015);
}

.component li > img {
  width: 1.38rem;
  height: 1.38rem;
  border-radius: 0.1rem;
  margin-right: 0.62rem;
}

.online::after {
  display: block;
  content: '';
  width: 0.62rem;
  height: 0.62rem;
  background: #42b72a;
  border-radius: 50%;
  margin-left: auto;
}

@media (max-width: 1000px) {
  .component {
    position: absolute;
    top: 0;
    right: 0;
    width: 18rem;
    -webkit-box-shadow: 0 0.38rem 0.62rem rgba(0, 0, 0, 0.1);
            box-shadow: 0 0.38rem 0.62rem rgba(0, 0, 0, 0.1);
    max-height: 62vh;
  }
}



