.component {
  margin: auto;
  text-align: center;
  color: #b6b6b6;
}

.component > * + * {
  margin-top: 1rem;
}

.component > span {
  font-size: 3rem;
}

.component > p {
  font-size: 1rem;
  line-height: 150%;
  max-width: 40ex;
}

.component svg {
  width: 100%;
  height: 6rem;
  opacity: 0.1;
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
}

@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



